@use "global/var" as v;
@use "global/mixins" as m;

html {
  font-size: 62.5%;
  box-sizing: border-box;
}
body {
  font-family: v.$FontFamily;
  font-size: 1.6rem;
  line-height: 1.8;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

.content {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

h1 {
  font-size: 3rem;
}
h2 {
  font-size: 2.7rem;
}
h3 {
  font-size: 2.2rem;
}
h4 {
  font-size: 2rem;
}
h1,
h2,
h3,
h4 {
  margin: 0;
}
.meta-dato {
  color: v.$GreenWhite;
}
.meta-dato2 {
  color: v.$Orange;
  font-weight: bold;
}
img {
  width: 100%;
  display: block;
}

a {
  text-decoration: none;
}

.Enlace-Activo {
  font-weight: bold;
}
