@use "global/var" as v;
@use "global/mixins" as m;

.SeccionContacto {
  background-image: linear-gradient(
      90deg,
      rgba(0, 0, 0, 0.75),
      hsla(0, 0%, 5%, 0.6)
    ),
    url(../images/cheeseTable.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  padding: 10rem 1rem;
}

.SeccionContacto-informacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  h2 {
    font-size: 4rem;
  }
  color: v.$White;
}
