@use "global/var" as v;
@use "global/mixins" as m;

.Nosotros {
  h2 {
    font-size: 3.5rem;
    text-align: center;
    padding: 4rem;
  }

  .Nosotros__Informcion {
    display: flex;
    flex-direction: column;
    @include m.desktop {
      flex-direction: row;
      align-items: center;
      gap: 2rem;
    }
  }
}

.Nosotros_ContenedorInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
  img {
    width: 10rem;
  }
  h3 {
    font-weight: bold;
    text-transform: uppercase;
  }
}

.ubicacion {
  h2 {
    margin-top: 4rem;
    text-align: center;
  }
}
