@use "global/var" as v;
@use "global/mixins" as m;
.PaginaAnuncio {
  h1 {
    text-align: center;
  }
  .Lampara {
    .Lampara__TopImage {
      height: 40rem;
      object-fit: cover;
    }
    .LamparaTexto {
      .LamparaTextoDatos {
        display: flex;
        justify-content: space-around;
        img {
          width: 5rem;
        }
        .meta-dato {
          font-size: 2.5rem;
          font-weight: bold;
        }
      }
      p {
        padding: 2rem;
        font-size: 2rem;
      }
    }
  }
}

.icon2 {
  display: flex;
  align-items: center;
  gap: 2rem;
  p {
    text-transform: uppercase;
    font-weight: bold;
    span {
      font-weight: normal;
    }
  }
}
