@use "global/var" as v;
@use "global/mixins" as m;

.SeccionBlogs {
  padding: 2rem;
  h3 {
    text-align: center;
    margin-bottom: 4rem;
  }
}
.Articulos {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
  a {
    color: v.$Dark;
  }
  .Articulo {
    @include m.desktop {
      display: grid;
      grid-template-columns: 1fr 2fr;
      column-gap: 2rem;
    }
    img {
      width: 25rem;
    }

    .Blog__Tittle {
      border-bottom: 2px solid #d44cc2;
      width: 50%;
    }
  }
}

.SeccionBlogs {
  .Articulos {
    a {
      color: #000;
    }
  }
}
