@use "global/var" as v;
@use "global/mixins" as m;

.Header__Complet {
  background-image: linear-gradient(
      90deg,
      rgba(0, 0, 0, 0.75),
      hsla(0, 0%, 5%, 0.6)
    ),
    url(../images/header.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  color: v.$White;
}

.Header__Complet--Content {
  min-height: 70rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.Header_Top {
  display: flex;
  flex-direction: column;
  align-items: center;
  @include m.desktop {
    flex-direction: row;
    justify-content: space-between;
    align-items: end;
  }
  .Logo {
    font-size: 4rem;
    text-transform: uppercase;
    font-weight: 200;
    margin: 0;
    span {
      font-weight: bold;
    }
  }
}

.Header_Bottom {
  font-size: 4rem;
  text-align: left;
  max-width: 70rem;
  font-weight: 700;
  margin: 2.5rem;
}

.Header__Nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  @include m.desktop {
    flex-direction: row;
    gap: 2rem;
  }
  a {
    color: v.$White;
    &:hover {
      color: v.$GreenWhite;
    }
  }
}
.HeaderSinBg {
  background-color: #333;
  padding: 2rem;
  .Logo {
    color: v.$White;
  }
}
