@use "global/var" as v;
@use "global/mixins" as m;

.footer {
  margin-top: 10rem;
  background-color: v.$Soft-Gray;
  padding: 2rem;
  color: v.$White;
  p {
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
  }
  nav {
    a {
      color: v.$White;
      @include m.desktop {
        margin-left: 2rem;
      }
    }
  }
}
