@use "global/var" as v;
@use "global/mixins" as m;

.Contacto {
  h1,
  h3 {
    text-align: center;
  }
  h3 {
    font-weight: normal;
  }
  img {
    height: 30rem;
    object-fit: cover;
  }
  .Formulario__Contacto {
    fieldset {
      display: flex;
      flex-direction: column;
      input {
        padding: 1rem;
      }
      input,
      textarea {
        margin-bottom: 2rem;
        border-radius: 10px;
        border: 1px solid #e1e1e1;
        background-color: #fff;
      }
      margin-bottom: 2rem;
    }
  }
}
