@use "global/var" as v;
@use "global/mixins" as m;

.Lamparas {
  h2 {
    text-align: center;
    padding: 4rem;
  }
}

.LamparaContenedor {
  @include m.desktop {
    @include m.grid(3, 3rem);
  }
  .Lampara {
    margin-bottom: 2rem;
    img {
      max-height: 20rem;
    }
    .LamparaTexto {
      background-color: v.$Off-White;
      padding: 1rem;
      h3 {
        text-align: center;
      }
      .meta-dato {
        font-size: 2.4rem;
        font-weight: bold;
      }
    }
  }
}
