/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.content {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.7rem;
}

h3 {
  font-size: 2.2rem;
}

h4 {
  font-size: 2rem;
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

.meta-dato {
  color: #71b100;
}

.meta-dato2 {
  color: #e08709;
  font-weight: bold;
}

img {
  width: 100%;
  display: block;
}

a {
  text-decoration: none;
}

.Enlace-Activo {
  font-weight: bold;
}

.Boton-1 {
  background-color: #d44cc2;
  color: #ffffff;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 5rem/2;
  display: block;
  border: none;
}
.Boton-1:hover {
  background-color: rgb(206.4216216216, 52.7783783784, 186.0864864865);
  cursor: pointer;
}

.Boton-2 {
  background-color: #d44cc2;
  color: #ffffff;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 5rem/2;
  display: inline;
  border: none;
}
.Boton-2:hover {
  background-color: rgb(206.4216216216, 52.7783783784, 186.0864864865);
  cursor: pointer;
}

/* Build */
.Header__Complet {
  background-image: linear-gradient(90deg, rgba(0, 0, 0, 0.75), hsla(0, 0%, 5%, 0.6)), url(../images/header.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  color: #ffffff;
}

.Header__Complet--Content {
  min-height: 70rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Header_Top {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1024px) {
  .Header_Top {
    flex-direction: row;
    justify-content: space-between;
    align-items: end;
  }
}
.Header_Top .Logo {
  font-size: 4rem;
  text-transform: uppercase;
  font-weight: 200;
  margin: 0;
}
.Header_Top .Logo span {
  font-weight: bold;
}

.Header_Bottom {
  font-size: 4rem;
  text-align: left;
  max-width: 70rem;
  font-weight: 700;
  margin: 2.5rem;
}

.Header__Nav {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1024px) {
  .Header__Nav {
    flex-direction: row;
    gap: 2rem;
  }
}
.Header__Nav a {
  color: #ffffff;
}
.Header__Nav a:hover {
  color: #71b100;
}

.HeaderSinBg {
  background-color: #333;
  padding: 2rem;
}
.HeaderSinBg .Logo {
  color: #ffffff;
}

.Nosotros h2 {
  font-size: 3.5rem;
  text-align: center;
  padding: 4rem;
}
.Nosotros .Nosotros__Informcion {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .Nosotros .Nosotros__Informcion {
    flex-direction: row;
    align-items: center;
    gap: 2rem;
  }
}

.Nosotros_ContenedorInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
}
.Nosotros_ContenedorInfo img {
  width: 10rem;
}
.Nosotros_ContenedorInfo h3 {
  font-weight: bold;
  text-transform: uppercase;
}

.ubicacion h2 {
  margin-top: 4rem;
  text-align: center;
}

.Lamparas h2 {
  text-align: center;
  padding: 4rem;
}

@media (min-width: 1024px) {
  .LamparaContenedor {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.LamparaContenedor .Lampara {
  margin-bottom: 2rem;
}
.LamparaContenedor .Lampara img {
  max-height: 20rem;
}
.LamparaContenedor .Lampara .LamparaTexto {
  background-color: #eee;
  padding: 1rem;
}
.LamparaContenedor .Lampara .LamparaTexto h3 {
  text-align: center;
}
.LamparaContenedor .Lampara .LamparaTexto .meta-dato {
  font-size: 2.4rem;
  font-weight: bold;
}

.SeccionContacto {
  background-image: linear-gradient(90deg, rgba(0, 0, 0, 0.75), hsla(0, 0%, 5%, 0.6)), url(../images/cheeseTable.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  padding: 10rem 1rem;
}

.SeccionContacto-informacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #ffffff;
}
.SeccionContacto-informacion h2 {
  font-size: 4rem;
}

.SeccionBlogs {
  padding: 2rem;
}
.SeccionBlogs h3 {
  text-align: center;
  margin-bottom: 4rem;
}

.Articulos {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
}
.Articulos a {
  color: #000;
}
@media (min-width: 1024px) {
  .Articulos .Articulo {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.Articulos .Articulo img {
  width: 25rem;
}
.Articulos .Articulo .Blog__Tittle {
  border-bottom: 2px solid #d44cc2;
  width: 50%;
}

.SeccionBlogs .Articulos a {
  color: #000;
}

@media (min-width: 1024px) {
  .Mision__And__Blog {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}

.Mision {
  padding: 2rem;
}
.Mision h3 {
  text-align: center;
  margin-bottom: 7rem;
}

.misiontext {
  background-color: #d44cc2;
  font-size: 2.5rem;
  padding: 2rem;
  border-radius: 2rem;
  color: #fff;
}

.footer {
  margin-top: 10rem;
  background-color: #2e2e38;
  padding: 2rem;
  color: #ffffff;
}
.footer p {
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
}
.footer nav a {
  color: #ffffff;
}
@media (min-width: 1024px) {
  .footer nav a {
    margin-left: 2rem;
  }
}

.DarkModeAndNav {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .DarkModeAndNav {
    align-items: end;
  }
}

.dark-mode {
  width: 5rem;
  filter: invert(100%);
}

.Dark__Mode--Active {
  background-color: #1a1a1a;
  color: #ffffff;
}
.Dark__Mode--Active .Lamparas .LamparaContenedor .Lampara .LamparaTexto {
  background-color: #333;
}
.Dark__Mode--Active .Mision__And__Blog .Articulos .Articulo {
  color: #ffffff;
}
.Dark__Mode--Active .SeccionBlogs .Articulos a {
  color: #ffffff;
}

.mobileversion {
  width: 3rem;
}
@media (min-width: 1024px) {
  .mobileversion {
    display: none;
  }
}

.Header__Nav {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
.Header__Nav.Active {
  visibility: visible;
  opacity: 1;
  height: auto;
}
@media (min-width: 1024px) {
  .Header__Nav {
    opacity: 1;
    visibility: visible;
  }
}

.NosotrosContent h1 {
  text-align: center;
  margin-bottom: 2rem;
}
.NosotrosContent .Nosotros {
  border-bottom: 1px solid #333;
}
@media (min-width: 1024px) {
  .NosotrosContent .Nosotros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 3rem;
    place-items: center;
  }
}
.NosotrosContent .Nosotros .Nosotros__imagen {
  margin-bottom: 2rem;
}
.NosotrosContent .Nosotros .Nosotros__imagen img {
  width: 100%;
}

.PaginaAnuncio h1 {
  text-align: center;
}
.PaginaAnuncio .Lampara .Lampara__TopImage {
  height: 40rem;
  object-fit: cover;
}
.PaginaAnuncio .Lampara .LamparaTexto .LamparaTextoDatos {
  display: flex;
  justify-content: space-around;
}
.PaginaAnuncio .Lampara .LamparaTexto .LamparaTextoDatos img {
  width: 5rem;
}
.PaginaAnuncio .Lampara .LamparaTexto .LamparaTextoDatos .meta-dato {
  font-size: 2.5rem;
  font-weight: bold;
}
.PaginaAnuncio .Lampara .LamparaTexto p {
  padding: 2rem;
  font-size: 2rem;
}

.icon2 {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.icon2 p {
  text-transform: uppercase;
  font-weight: bold;
}
.icon2 p span {
  font-weight: normal;
}

.Contacto h1,
.Contacto h3 {
  text-align: center;
}
.Contacto h3 {
  font-weight: normal;
}
.Contacto img {
  height: 30rem;
  object-fit: cover;
}
.Contacto .Formulario__Contacto fieldset {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.Contacto .Formulario__Contacto fieldset input {
  padding: 1rem;
}
.Contacto .Formulario__Contacto fieldset input,
.Contacto .Formulario__Contacto fieldset textarea {
  margin-bottom: 2rem;
  border-radius: 10px;
  border: 1px solid #e1e1e1;
  background-color: #fff;
}