@use "global/var" as v;
@use "global/mixins" as m;

.NosotrosContent {
  h1 {
    text-align: center;
    margin-bottom: 2rem;
  }

  .Nosotros {
    border-bottom: 1px solid #333;

    @include m.desktop {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 3rem;
      place-items: center;
    }
    .Nosotros__imagen {
      margin-bottom: 2rem;
      img {
        width: 100%;
      }
    }
  }
}
